.about-section {
  padding: 32px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 24px;
  background: rgb(131, 58, 180);
  background: linear-gradient(
    90deg,
    rgba(131, 58, 180, 0.4206932773109243) 0%,
    rgba(253, 29, 29, 0.41228991596638653) 50%,
    rgba(255, 171, 167, 0.9136904761904762) 100%
  );
}
/* Image */
.about-image-content {
  width: 50%;
  max-width: 100%;
  text-align: center;
}
.about-image1 {
  width: 80%;
  height: auto;
}
/* Text */
.about-text-content {
  width: 50%;
}
.about-title {
  font-family: "Poppins", sans-serif;
  font-size: 40px;
  font-weight: bold;
}
.about-title > span {
  position: relative;
}
.about-title span::before {
  content: "";
  position: absolute;
  width: 75%;
  height: 4px;
  background-color: #9d2553;
  border-radius: 8px;
  left: 0;
  bottom: -12px;
}
.about-description {
  margin: 32px 0;
  color: #4d4b4b;
  font-family: "Rubik", sans-serif;
  font-size: 18px;
  letter-spacing: 0.7px;
  line-height: 1.7rem;
}
.about-text-title {
  margin: 0 0 24px 0;
  color: #4d4b4b;
  font-family: "Poppins", sans-serif;
  font-size: 24px;
  font-weight: bold;
  letter-spacing: 0.7px;
}
/* Steps */
.about-text-step {
  margin: 16px 0;
}
.about-text-sTitle {
  font-family: "Rubik", sans-serif;
  font-size: 18px;
  font-weight: bold;
  letter-spacing: 0.7px;
}
.about-text-description {
  margin: 8px 24px;
  color: #4d4b4b;
  font-family: "Rubik", sans-serif;
  letter-spacing: 0.7px;
  line-height: 1.6rem;
}
.fa-icon {
  color: #9d2553;
}
/* Responsive */
@media screen and (max-width: 1000px) {
  .about-section {
    display: block;
  }
  .about-image-content {
    width: 100%;
    max-width: 100%;
    margin: 0 0 32px 0;
  }
  .about-image1 {
    width: 100%;
    height: 100%;
  }
  .about-text-content {
    width: 100%;
  }
}
